<template>
  <div class="about">
    <img :src="imgUrl" alt="">
    <h4>{{title}}</h4>
    <p>{{summary}}</p>
  </div>
</template>
<script>
import axios from "axios-jsonp-pro";
export default {
  name:"About",
  data(){
    return{
      imgUrl:"",
      title:"",
      summary:""
    }
  },
  mounted(){
    var id = this.$route.params.id;
    var url = "http://api.douban.com/v2/movie/subject/";
    axios.jsonp(url+id).then(res=>{
      this.imgUrl = res.images.small;
      this.title = res.title;
      this.summary = res.summary;
    })
  }
}
</script>
<style lang="scss" scoped>
.about{
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
</style>
